.blog {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 50px;

    @media(max-width: 600px) {
        flex-direction: column;
    }

    &__post {
        display: flex;
        flex-direction: column;
        max-width: 380px;
        width: 100%;

        @media(max-width: 768px) {
            max-width: 32%;
        }
        @media(max-width: 600px) {
            max-width: 380px;
            
            &:not(:last-child) {
                margin-bottom: 30px;
            }
        }

        &-img {
            display: block;
            height: 280px;
            margin-bottom: 15px;
            position: relative;
            width: 100%;

            @media(max-width: 768px) {
                height: 200px;
            }

            &::after {
                content: '';
                background-color: rgba(#000, .5);
                height: 100%;
                left: 0;
                position: absolute;
                top: 0;
                transition: background .3s ease;
                width: 100%;
            }

            &:hover {
                &::after {
                    background-color: rgba(#000, .7);
                }
            }

            strong {
                background-color: var(--background);
                color: var(--primary-text);
                font-size: 22px;
                line-height: 1;
                left: 0;
                margin: 10px 0 0 10px;
                padding: 6px 12px;
                position: absolute;
                text-align: center;
                top: 0;
                z-index: 3;

                span {
                    display: block;
                    line-height: 1;
                    font-size: 16px;
                    font-family: 'Outfit', sans-serif;
                    font-weight: 400;
                }
            }

            img {
                display: block;
                height: 100%;
                object-fit: cover;               
                width: 100%;
            }
        }

        h2 {
            font-size: 22px;
            line-height: 1.2;
            margin-bottom: 10px;

            @media(max-width: 768px) {
                font-size: 16px;
            }

            @media(max-width: 600px) {
                font-size: 18px;
            }

             a {
                color: var(--primary);
                font-family: "Plus Jakarta Sans", sans-serif;
                font-weight: 400;
                font-optical-sizing: auto;
                text-decoration: none;
                transition: all .3s ease-in-out;

                &:hover {
                    color: var(--secondary);
                }
             }
        }

        p {
            color: var(--primary-text);
            font-family: 'Outfit', sans-serif;
            font-size: 16px;
            font-weight: 300;
            line-height: 1.5;

            @media(max-width: 768px) {
                font-size: 14px;
            }
        }
    }

    &__btn {
        display: block;
        margin: 0 auto 100px;
        width: fit-content;
    }
}