footer {
    .newsletter {
        align-items: center;
        background-color: rgba(var(--primary-rgb), .1);
        border-top: solid 1px var(--primary);
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 50px 0;

        @media(max-width: 1200px) {
            padding: 50px 20px;
        }
        
        h3 {
            color: var(--primary);
            font-family: 'Outfit', sans-serif;
            font-size: 24px;
            font-weight: 200;
            margin-bottom: 20px;

            @media(max-width: 1200px) {
                font-size: 18px;
                text-align: center;
            }
        }

        form {

            display: flex;
            align-items: center;

            input {
                border: 0;
                background-color: rgba(var(--primary-rgb), .1);
                color: var(--primary);
                outline: none;
                height: 50px;
                font-size: 16px;
                font-family: 'Outfit', sans-serif;
                font-weight: 400;
                max-width: 100%;
                padding: 15px;
                width: 380px;

                @media(max-width: 1200px) {
                    width: 90%;
                }
            }

            button {
                background-color: var(--primary);
                border: 0;
                color: #fff;
                cursor: pointer;
                height: 50px;
                font-family: 'Outfit', sans-serif;
                font-size: 16px;
                font-weight: 400;
                outline: none;
                transition: all .3s ease-in-out;
                width: 100px;

                @media(max-width: 1200px) {
                    font-size: 12px;
                    padding: 0 10px;
                    width: auto;
                }

                &:hover {
                    background-color: var(--secondary);
                }
            }

            ::placeholder {
                color: var(--primary);
                font-size: 16px;
                font-weight: 300;

                @media(max-width: 1200px) {
                    font-size: 14px;
                }
            }
        }
    }

    .ftr {
        display: flex;
        justify-content: space-between;
        padding: 50px 70px 40px;

        @media(max-width: 1200px) {
            flex-direction: column;
        }

        &__logo {
            align-items: center;
            display: flex;
            flex-direction: column;
            justify-content: center;

            .logo {
                img {
                    display: block;
                    object-fit: contain;
                    object-position: center;
                    image-rendering: -webkit-optimize-contrast;
                    height: 80px;
                    margin-bottom: 30px;
                    width: 185px;
                }
            }

            ul {
                display: flex;
                justify-content: space-between;
                max-width: 120px;
                width: 100%;

                li {
                    a {

                        img {
                            display: block;
                            transition: all .3s ease-in-out;
                        }

                        &:hover {
                            img {
                                opacity: .8;
                            }
                        }
                    }
                }
            }
        }

        &__wrap {
            display: flex;
            justify-content: space-between;
            max-width: 100%;
            width: 630px;

            @media(max-width: 1200px) {
               display: none;
            }
        }

        &__mnu {
            display: flex;
            flex-direction: column;

            h4 {
                color: var(--primary);
                font-family: 'Outfit', sans-serif;
                font-size: 24px;
                font-weight: 500;
                margin-bottom: 24px;
            }

            ul {
                li {
                    a {
                        color: var(--primary);
                        font-family: 'Outfit', sans-serif;
                        font-weight: 300;
                        text-decoration: none;
                        transition: all .3s ease-in-out;

                        &:hover {
                            font-weight: 400;
                            text-decoration: underline;
                        }
                    }
                }
            }
        }
    }

    .copyright {
        background-color: var(--primary);
        padding: 6px 0;
        p {
            color: #fff;
            font-family: 'Outfit', sans-serif;
            font-size: 14px;
            text-align: center;
        }
    }
}