h3.title {
    color: var(--primary);
    font-size: 28px;
    font-weight: 600;
    line-height: 1.2;
    margin-bottom: 8px;
    position: relative;

    @media(max-width: 600px) {
        font-size: 22px;
    }
}

p.sub-title {
    color: var(--primary-text);
    font-family: 'Outfit', sans-serif;
    font-size: 14px;
    font-weight: 300;
    margin-bottom: 50px;
    max-width: 420px;
    position: relative;

    &:after {
        content: '';
        background-color: var(--primary);
        height: 2px;
        left: 0;
        position: absolute;
        bottom: -25px;
        width: 100px;
    }
}