.productCard {
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    max-width: 270px;
    position: relative;
    width: 100%;

    @media(max-width: 1200px) {
        max-width: 100%;
    }

    &__info {
        display: flex;
        height: 350px;
        flex-direction: column;
        justify-content: space-between;
        margin-bottom: 10px;
        overflow: hidden;
        width: 100%;

        .img {
            display: block;
            height: 350px;
            max-width: 100%;
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            
            &::after {
                content: '';
                background-color: rgba(#000, .1);
                position: inherit;
                left: inherit;
                top: inherit;
                height: 100%;
                width: 100%;
            }

            img {
                object-fit: contain;
                object-position: top;
                height: 100%;
                width: 100%;
                
                @media(max-width: 1200px) {
                    object-fit: cover;
                }
            }
        }

        span {
            background-color: var(--background);
            // border: solid 1px var(--primary);
            color: var(--primary-text);
            display: inline-block;
            font-family: 'Outfit', sans-serif;
            font-size: 12px;
            font-weight: 300;
            letter-spacing: 1.2px;
            margin: 10px 0 0 0;
            padding: 4px 8px;
            position: relative;
            width: fit-content;
            z-index: 1;
        }

        .btnsControl {
            bottom: -50px;
            display: flex;
            align-items: center;
            margin-top: auto;
            position: relative;
            transition: all .3s ease-in-out;
            z-index: 1;
            
            .btnBuy, .btnFav, .btnView {
                background-color: rgba(var(--terciary-rgb), .3);
                border-top: solid 1px #ededed;
                color: #fff;
                display: flex;
                align-items: center;
                justify-content: center;
                height: 40px;
                text-align: center;
                text-decoration: none;
                transition:  all .3s ease-in-out;
                width: 15%;

                i {
                    display: inline-block;
                    font-size: 18px;
                    vertical-align: middle;
                }

                &:hover {
                    background-color: var(--terciary);
                }
            }

            .btnFav {
                border-left: solid 1px #ededed;
                border-right: solid 1px #ededed;
            }

            .btnBuy {
                font-family: "Outfit";
                font-size: 12px;
                text-align: center;
                width: 70%;
                
                i {
                    margin-left: 5px;
                }
            }

            &.active {
                bottom: 0;
            }
        }

    }
    h3 {
        a {
            color: var(--primary);
            display: inline-block;
            text-decoration: none;
        }
    }
    p {
        color: var(--primary-text);
        font-family: 'Outfit', sans-serif;
    }
}


.release {
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;

    @media(max-width: 1200px) {
        .productCard {
            &:not(:first-child) {
                display: none;
            }
        }
    }
}