html, body {
  -webkit-font-smoothing: antialiased;
  font-family: 'Conso', sans-serif;
  font-size: 100%;
  -webkit-tap-highlight-color: transparent;
  overflow-x: hidden; 
}

*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
}

p,
li,
span,
button,
input,
textarea,
table,
td,
th,
a,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Conso', sans-serif;
}


.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;

  @media(max-width: 1200px){
    max-width: 100%;
    width: 90%;
  }
}

.hide {
  display: none;
}

.show {
  display: inline-block;
}