@font-face {
    font-family: 'Conso';
    src: url('../fonts/Conso-MediumItalic.eot');
    src: local('Conso Medium Italic'), local('../fonts/Conso-MediumItalic'),
        url('../fonts/Conso-MediumItalic.eot?#iefix') format('embedded-opentype'),
        url('../fonts/Conso-MediumItalic.woff2') format('woff2'),
        url('../fonts/Conso-MediumItalic.woff') format('woff'),
        url('../fonts/Conso-MediumItalic.ttf') format('truetype');
    font-weight: 500;
    font-style: italic;
    font-display: swap;
}

