html, body {
  -webkit-font-smoothing: antialiased;
  font-family: "Conso", sans-serif;
  font-size: 100%;
  -webkit-tap-highlight-color: transparent;
  overflow-x: hidden;
}

*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
}

p,
li,
span,
button,
input,
textarea,
table,
td,
th,
a,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Conso", sans-serif;
}

.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}
@media (max-width: 1200px) {
  .container {
    max-width: 100%;
    width: 90%;
  }
}

.hide {
  display: none;
}

.show {
  display: inline-block;
}

header {
  background-color: #fff;
  border-color: #fff;
  padding: 0 30px;
  position: relative;
  transition: all 0.3s ease-out;
  width: 100%;
  z-index: 999;
}
@media (max-width: 1200px) {
  header {
    border-bottom: solid 1px var(--primary);
    padding: 0;
  }
}
header.fixed {
  border-bottom: solid 1px var(--primary);
  position: fixed;
  top: 0;
  left: 0;
}
header nav {
  align-items: center;
  display: flex;
  height: 100px;
  justify-content: space-between;
  width: 100%;
}
@media (max-width: 1200px) {
  header nav {
    height: 80px;
    align-items: flex-end;
    justify-content: initial;
    padding-bottom: 10px;
  }
}
header nav .logo {
  display: block;
}
header nav .logo img {
  display: block;
  height: 80px;
  object-fit: contain;
  object-position: center;
  image-rendering: -webkit-optimize-contrast;
  max-width: 100%;
  width: 185px;
}
@media (max-width: 1200px) {
  header nav .logo img {
    height: 60px;
    width: auto;
  }
}
header nav ul {
  align-items: center;
  background-color: #fff;
  list-style: none;
  display: flex;
  justify-content: space-between;
  width: 60%;
}
@media (max-width: 1200px) {
  header nav ul {
    align-items: flex-end;
    flex-direction: column;
    height: calc(100vh - 80px);
    justify-content: start;
    position: absolute;
    right: -3000px;
    top: 80px;
    transition: all 0.3s ease-in-out;
    width: 100%;
  }
}
header nav ul.active {
  right: 0;
}
header nav ul li {
  height: 35px;
}
@media (max-width: 1200px) {
  header nav ul li {
    height: 50px;
    width: 100%;
  }
}
header nav ul li:last-child {
  display: none;
}
@media (max-width: 1200px) {
  header nav ul li:last-child {
    display: block;
  }
  header nav ul li:last-child a i {
    font-size: 24px;
  }
}
header nav ul li a {
  color: var(--primary);
  display: inline-block;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  padding: 6px 20px;
  text-decoration: none;
  transition: all 0.3s ease-in;
}
header nav ul li a:hover:not(.active), header nav ul li a.active {
  background-color: var(--primary);
  color: #fff;
}
@media (max-width: 1200px) {
  header nav ul li a {
    display: block;
    padding: 0 15px;
    line-height: 50px;
  }
}
header nav .userActions {
  display: flex;
  align-items: center;
}
@media (max-width: 1200px) {
  header nav .userActions {
    margin-left: auto;
  }
}
header nav .userActions a {
  display: block;
}
header nav .userActions a i {
  color: var(--primary);
  font-size: 24px;
}
header nav .userActions a:first-child {
  margin-right: 15px;
}
@media (max-width: 1200px) {
  header nav .userActions a:first-child {
    display: none;
  }
}
header nav .userActions a:hover i {
  color: var(--secondary);
}
header nav .mobMenu {
  display: block;
  margin-left: 20px;
  outline: none;
}
@media (min-width: 1200px) {
  header nav .mobMenu {
    display: none;
  }
}
header nav .mobMenu:hover i {
  color: var(--secondary);
}
header nav .mobMenu i {
  font-size: 24px;
  color: var(--primary);
}
header nav .mobMenu i:not(.show) {
  display: none;
}

footer .newsletter {
  align-items: center;
  background-color: rgba(var(--primary-rgb), 0.1);
  border-top: solid 1px var(--primary);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 50px 0;
}
@media (max-width: 1200px) {
  footer .newsletter {
    padding: 50px 20px;
  }
}
footer .newsletter h3 {
  color: var(--primary);
  font-family: "Outfit", sans-serif;
  font-size: 24px;
  font-weight: 200;
  margin-bottom: 20px;
}
@media (max-width: 1200px) {
  footer .newsletter h3 {
    font-size: 18px;
    text-align: center;
  }
}
footer .newsletter form {
  display: flex;
  align-items: center;
}
footer .newsletter form input {
  border: 0;
  background-color: rgba(var(--primary-rgb), 0.1);
  color: var(--primary);
  outline: none;
  height: 50px;
  font-size: 16px;
  font-family: "Outfit", sans-serif;
  font-weight: 400;
  max-width: 100%;
  padding: 15px;
  width: 380px;
}
@media (max-width: 1200px) {
  footer .newsletter form input {
    width: 90%;
  }
}
footer .newsletter form button {
  background-color: var(--primary);
  border: 0;
  color: #fff;
  cursor: pointer;
  height: 50px;
  font-family: "Outfit", sans-serif;
  font-size: 16px;
  font-weight: 400;
  outline: none;
  transition: all 0.3s ease-in-out;
  width: 100px;
}
@media (max-width: 1200px) {
  footer .newsletter form button {
    font-size: 12px;
    padding: 0 10px;
    width: auto;
  }
}
footer .newsletter form button:hover {
  background-color: var(--secondary);
}
footer .newsletter form ::placeholder {
  color: var(--primary);
  font-size: 16px;
  font-weight: 300;
}
@media (max-width: 1200px) {
  footer .newsletter form ::placeholder {
    font-size: 14px;
  }
}
footer .ftr {
  display: flex;
  justify-content: space-between;
  padding: 50px 70px 40px;
}
@media (max-width: 1200px) {
  footer .ftr {
    flex-direction: column;
  }
}
footer .ftr__logo {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
footer .ftr__logo .logo img {
  display: block;
  object-fit: contain;
  object-position: center;
  image-rendering: -webkit-optimize-contrast;
  height: 80px;
  margin-bottom: 30px;
  width: 185px;
}
footer .ftr__logo ul {
  display: flex;
  justify-content: space-between;
  max-width: 120px;
  width: 100%;
}
footer .ftr__logo ul li a img {
  display: block;
  transition: all 0.3s ease-in-out;
}
footer .ftr__logo ul li a:hover img {
  opacity: 0.8;
}
footer .ftr__wrap {
  display: flex;
  justify-content: space-between;
  max-width: 100%;
  width: 630px;
}
@media (max-width: 1200px) {
  footer .ftr__wrap {
    display: none;
  }
}
footer .ftr__mnu {
  display: flex;
  flex-direction: column;
}
footer .ftr__mnu h4 {
  color: var(--primary);
  font-family: "Outfit", sans-serif;
  font-size: 24px;
  font-weight: 500;
  margin-bottom: 24px;
}
footer .ftr__mnu ul li a {
  color: var(--primary);
  font-family: "Outfit", sans-serif;
  font-weight: 300;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
}
footer .ftr__mnu ul li a:hover {
  font-weight: 400;
  text-decoration: underline;
}
footer .copyright {
  background-color: var(--primary);
  padding: 6px 0;
}
footer .copyright p {
  color: #fff;
  font-family: "Outfit", sans-serif;
  font-size: 14px;
  text-align: center;
}

.productCard {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  max-width: 270px;
  position: relative;
  width: 100%;
}
@media (max-width: 1200px) {
  .productCard {
    max-width: 100%;
  }
}
.productCard__info {
  display: flex;
  height: 350px;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 10px;
  overflow: hidden;
  width: 100%;
}
.productCard__info .img {
  display: block;
  height: 350px;
  max-width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
}
.productCard__info .img::after {
  content: "";
  background-color: rgba(0, 0, 0, 0.1);
  position: inherit;
  left: inherit;
  top: inherit;
  height: 100%;
  width: 100%;
}
.productCard__info .img img {
  object-fit: contain;
  object-position: top;
  height: 100%;
  width: 100%;
}
@media (max-width: 1200px) {
  .productCard__info .img img {
    object-fit: cover;
  }
}
.productCard__info span {
  background-color: var(--background);
  color: var(--primary-text);
  display: inline-block;
  font-family: "Outfit", sans-serif;
  font-size: 12px;
  font-weight: 300;
  letter-spacing: 1.2px;
  margin: 10px 0 0 0;
  padding: 4px 8px;
  position: relative;
  width: fit-content;
  z-index: 1;
}
.productCard__info .btnsControl {
  bottom: -50px;
  display: flex;
  align-items: center;
  margin-top: auto;
  position: relative;
  transition: all 0.3s ease-in-out;
  z-index: 1;
}
.productCard__info .btnsControl .btnBuy, .productCard__info .btnsControl .btnFav, .productCard__info .btnsControl .btnView {
  background-color: rgba(var(--terciary-rgb), 0.3);
  border-top: solid 1px #ededed;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  text-align: center;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
  width: 15%;
}
.productCard__info .btnsControl .btnBuy i, .productCard__info .btnsControl .btnFav i, .productCard__info .btnsControl .btnView i {
  display: inline-block;
  font-size: 18px;
  vertical-align: middle;
}
.productCard__info .btnsControl .btnBuy:hover, .productCard__info .btnsControl .btnFav:hover, .productCard__info .btnsControl .btnView:hover {
  background-color: var(--terciary);
}
.productCard__info .btnsControl .btnFav {
  border-left: solid 1px #ededed;
  border-right: solid 1px #ededed;
}
.productCard__info .btnsControl .btnBuy {
  font-family: "Outfit";
  font-size: 12px;
  text-align: center;
  width: 70%;
}
.productCard__info .btnsControl .btnBuy i {
  margin-left: 5px;
}
.productCard__info .btnsControl.active {
  bottom: 0;
}
.productCard h3 a {
  color: var(--primary);
  display: inline-block;
  text-decoration: none;
}
.productCard p {
  color: var(--primary-text);
  font-family: "Outfit", sans-serif;
}

.release {
  display: flex;
  justify-content: space-between;
  margin-bottom: 50px;
}
@media (max-width: 1200px) {
  .release .productCard:not(:first-child) {
    display: none;
  }
}

.blog {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
}
@media (max-width: 600px) {
  .blog {
    flex-direction: column;
  }
}
.blog__post {
  display: flex;
  flex-direction: column;
  max-width: 380px;
  width: 100%;
}
@media (max-width: 768px) {
  .blog__post {
    max-width: 32%;
  }
}
@media (max-width: 600px) {
  .blog__post {
    max-width: 380px;
  }
  .blog__post:not(:last-child) {
    margin-bottom: 30px;
  }
}
.blog__post-img {
  display: block;
  height: 280px;
  margin-bottom: 15px;
  position: relative;
  width: 100%;
}
@media (max-width: 768px) {
  .blog__post-img {
    height: 200px;
  }
}
.blog__post-img::after {
  content: "";
  background-color: rgba(0, 0, 0, 0.5);
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  transition: background 0.3s ease;
  width: 100%;
}
.blog__post-img:hover::after {
  background-color: rgba(0, 0, 0, 0.7);
}
.blog__post-img strong {
  background-color: var(--background);
  color: var(--primary-text);
  font-size: 22px;
  line-height: 1;
  left: 0;
  margin: 10px 0 0 10px;
  padding: 6px 12px;
  position: absolute;
  text-align: center;
  top: 0;
  z-index: 3;
}
.blog__post-img strong span {
  display: block;
  line-height: 1;
  font-size: 16px;
  font-family: "Outfit", sans-serif;
  font-weight: 400;
}
.blog__post-img img {
  display: block;
  height: 100%;
  object-fit: cover;
  width: 100%;
}
.blog__post h2 {
  font-size: 22px;
  line-height: 1.2;
  margin-bottom: 10px;
}
@media (max-width: 768px) {
  .blog__post h2 {
    font-size: 16px;
  }
}
@media (max-width: 600px) {
  .blog__post h2 {
    font-size: 18px;
  }
}
.blog__post h2 a {
  color: var(--primary);
  font-family: "Plus Jakarta Sans", sans-serif;
  font-weight: 400;
  font-optical-sizing: auto;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
}
.blog__post h2 a:hover {
  color: var(--secondary);
}
.blog__post p {
  color: var(--primary-text);
  font-family: "Outfit", sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 1.5;
}
@media (max-width: 768px) {
  .blog__post p {
    font-size: 14px;
  }
}
.blog__btn {
  display: block;
  margin: 0 auto 100px;
  width: fit-content;
}

.banner {
  background-color: rgba(var(--hover-rgb), 0.1);
  display: flex;
  height: calc(100vh - 100px);
  position: relative;
}
@media (max-width: 1200px) {
  .banner {
    height: calc(100vh - 80px);
    padding: 0 20px;
  }
}
.banner:after {
  content: none;
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  left: 0;
  height: 100%;
  top: 0;
  width: 100%;
  z-index: 1;
}
@media (max-width: 1200px) {
  .banner:after {
    content: "";
  }
}
.banner__txt {
  align-items: baseline;
  display: flex;
  flex-direction: column;
  margin: auto;
  position: relative;
  max-width: 450px;
  width: 100%;
  z-index: 3;
}
.banner__txt p {
  color: var(--primary-text);
  font-optical-sizing: auto;
  font-family: "Outfit", sans-serif;
  font-size: 20px;
  margin-bottom: 20px;
}
@media (max-width: 1200px) {
  .banner__txt p {
    color: #fff;
  }
}
.banner__txt h1 {
  color: var(--primary);
  font-optical-sizing: auto;
  font-size: 48px;
  line-height: 1.2;
  margin-bottom: 40px;
}
@media (max-width: 1200px) {
  .banner__txt h1 {
    font-size: 36px;
  }
}
.banner img {
  display: block;
  object-fit: cover;
  width: 50%;
}
@media (max-width: 1200px) {
  .banner img {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
  }
}

.buyInfos {
  background-color: var(--background);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 100px;
  padding: 20px 0;
}
@media (max-width: 600px) {
  .buyInfos {
    flex-direction: column;
    align-items: baseline;
    justify-content: space-between;
    padding: 20px 50px;
  }
}
.buyInfos li {
  align-items: center;
  display: flex;
  justify-content: center;
  max-width: 100%;
  width: 400px;
}
@media (max-width: 600px) {
  .buyInfos li {
    justify-content: flex-start;
  }
  .buyInfos li:not(:last-child) {
    margin-bottom: 20px;
  }
}
.buyInfos li img {
  display: block;
  height: 40px;
  margin-right: 15px;
}
.buyInfos li p {
  color: #4d4d4d;
  font-family: "Outfit", sans-serif;
  font-size: 12px;
  font-weight: 400;
}
.buyInfos li p strong {
  color: #363435;
  display: block;
  font-size: 20px;
  font-weight: 600;
  line-height: 24px;
}